@use 'abstracts/fonts' as *;
@use 'abstracts/colors' as *;

/* Font-family */
.ff-heading { font-family: $ff-heading };
.ff-body { font-family: $ff-body };
.ff-accent { font-family: $ff-accent };

/* Font-size */
.fs-900 { font-size: $fs-900 };
.fs-800 { font-size: $fs-800 };
.fs-700 { font-size: $fs-700 };
.fs-600 { font-size: $fs-600 };
.fs-500 { font-size: $fs-500 };
.fs-400 { font-size: $fs-400 };
.fs-300 { font-size: $fs-300 };
.fs-200 { font-size: $fs-200 };

/* Font-weight */
.fw-700 { font-weight: $fw-700 };
.fw-500 { font-weight: $fw-500 };
.fw-400 { font-weight: $fw-400 };
.fw-300 { font-weight: $fw-300 };

/* Line-height */
.fs-900, 
.fs-800, 
.fs-700 {
    line-height: 1.1;
}

/* Character spacing */
.letter-spacing-1 { letter-spacing: 5px; }

/* Random styles */
.uppercase { text-transform: uppercase;}

.numbered-title { 
    font-size: $fs-500;
    margin-bottom: 1rem;
    
    span {
        font-weight: $fw-700;
        color: $white-75;
        margin-right: .75em;
    }
    
}





